import React from "react";
import Matchup from "./Matchup";
import Columns from 'react-bulma-components/lib/components/columns';
import Heading from 'react-bulma-components/lib/components/heading';
import Progress from 'react-bulma-components/lib/components/progress';

class Header extends React.Component {

    render() {
      return (
          <div>
            <Columns>
                <Columns.Column size={1}>
                    <Matchup
                        round={0}
                        division={'y'}
                        matchId={0}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    >
                    </Matchup>
                </Columns.Column>
                <Columns.Column size={9}>
                    <Heading className={"title is-1"}>Stonk Madness</Heading>
                    <Heading className={"subtitle"}>Score as of {this.props.scoreDate}</Heading>
                </Columns.Column>
                <Columns.Column size={1}>
                    <Matchup
                        round={0}
                        division={'z'}
                        matchId={0}
                        teams={this.props.teams}
                        bracket={this.props.bracket}
                    >
                    </Matchup>
                </Columns.Column>
            </Columns>
            <Columns>
                <Columns.Column size={1}><Heading className={"title is-4"}>{this.props.rounds[0].name}</Heading></Columns.Column>
                <Columns.Column size={1}><Heading className={"title is-4"}>{this.props.rounds[1].name}</Heading></Columns.Column>
                <Columns.Column size={1}><Heading className={"title is-4"}>{this.props.rounds[2].name}</Heading></Columns.Column>
                <Columns.Column size={1}><Heading className={"title is-4"}>{this.props.rounds[3].name}</Heading></Columns.Column>
                <Columns.Column size={1}><Heading className={"title is-4"}>{this.props.rounds[4].name}</Heading></Columns.Column>
                <Columns.Column size={1}><Heading className={"title is-4"}>{this.props.rounds[5].name}</Heading></Columns.Column>
                <Columns.Column size={1}><Heading className={"title is-4"}>{this.props.rounds[4].name}</Heading></Columns.Column>
                <Columns.Column size={1}><Heading className={"title is-4"}>{this.props.rounds[3].name}</Heading></Columns.Column>
                <Columns.Column size={1}><Heading className={"title is-4"}>{this.props.rounds[2].name}</Heading></Columns.Column>
                <Columns.Column size={1}><Heading className={"title is-4"}>{this.props.rounds[1].name}</Heading></Columns.Column>
                <Columns.Column size={1}><Heading className={"title is-4"}>{this.props.rounds[0].name}</Heading></Columns.Column>
            </Columns>
            <Columns>
                <Columns.Column size={1}>{this.props.rounds[0].start} to {this.props.rounds[0].end}</Columns.Column>
                <Columns.Column size={1}>{this.props.rounds[1].start} to {this.props.rounds[1].end}</Columns.Column>
                <Columns.Column size={1}>{this.props.rounds[2].start} to {this.props.rounds[2].end}</Columns.Column>
                <Columns.Column size={1}>{this.props.rounds[3].start} to {this.props.rounds[3].end}</Columns.Column>
                <Columns.Column size={1}>{this.props.rounds[4].start} to {this.props.rounds[4].end}</Columns.Column>
                <Columns.Column size={1}>{this.props.rounds[5].start} to {this.props.rounds[5].end}</Columns.Column>
                <Columns.Column size={1}>{this.props.rounds[4].start} to {this.props.rounds[4].end}</Columns.Column>
                <Columns.Column size={1}>{this.props.rounds[3].start} to {this.props.rounds[3].end}</Columns.Column>
                <Columns.Column size={1}>{this.props.rounds[2].start} to {this.props.rounds[2].end}</Columns.Column>
                <Columns.Column size={1}>{this.props.rounds[1].start} to {this.props.rounds[1].end}</Columns.Column>
                <Columns.Column size={1}>{this.props.rounds[0].start} to {this.props.rounds[0].end}</Columns.Column>
            </Columns>
            <Columns>
                <Columns.Column size={1}><Progress className={'is-info'} value='20' max='22'>90%</Progress></Columns.Column>
                <Columns.Column size={1}><Progress className={'is-info'} value='0' max='100'>45%</Progress></Columns.Column>
                <Columns.Column size={1}><Progress className={'is-info'} value='0' max='100'>45%</Progress></Columns.Column>
                <Columns.Column size={1}><Progress className={'is-info'} value='0' max='100'>45%</Progress></Columns.Column>
                <Columns.Column size={1}><Progress className={'is-info'} value='0' max='100'>45%</Progress></Columns.Column>
                <Columns.Column size={1}><Progress className={'is-info'} value='0' max='100'>45%</Progress></Columns.Column>
                <Columns.Column size={1}><Progress className={'is-info'} value='0' max='100'>45%</Progress></Columns.Column>
                <Columns.Column size={1}><Progress className={'is-info'} value='0' max='100'>45%</Progress></Columns.Column>
                <Columns.Column size={1}><Progress className={'is-info'} value='0' max='100'>45%</Progress></Columns.Column>
                <Columns.Column size={1}><Progress className={'is-info'} value='0' max='100'>45%</Progress></Columns.Column>
                <Columns.Column size={1}><Progress className={'is-info'} value='20' max='22'>90%</Progress></Columns.Column>
            </Columns>
          </div>
        );
    }
}


export default Header;